@import url('https://fonts.googleapis.com/css2?family=Mitr:wght@500&display=swap');

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #036891;
  color: white;
}

.root {
  display: flex;
  flex-direction: column;

}

header {
  background-color: #055c72;
  display: flex;
  padding: .5rem;
  justify-content: space-around;
  box-shadow: 5px -2px 8px rgba(63, 62, 62, 0.568);

}
header h1{
   font-family: 'Mitr';
   letter-spacing: 3px;
   font-size: 2.5rem;
   text-shadow: 2px 3px 5px rgb(46, 45, 45);
   
}
header h1 span{
   font-family: 'Mitr';
   letter-spacing: 1px;
   color: rgba(248, 1, 1, 0.692);
  text-shadow: 2px 3px 5px rgb(46, 45, 45);
}
header form{
   width: 50%;
   margin-top: 1.2rem;
}

.search {
  background-color: transparent;

  box-shadow: inset 2px 2px 6px #01011b;
  border-radius: 1.75rem;
  color: #efeff3;
  font-family: inherit;
  font-size: 1.2rem;
  padding: .5rem 1.5rem;
  border: none;
  outline: none;
  margin-top: .9rem;
   width: 70%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.movie-container {
  display: flex;
  flex-wrap: wrap;
  margin-left: 1rem;
}

.movie {

  background-color: #015a69;
  margin-top: 1rem;
  width: 16rem;
  border-radius: .8rem;
  box-shadow: 5px -2px 8px rgba(231, 229, 229, 0.568);
  overflow: hidden;
  margin-left: 2.5rem;
  position: relative;
}
iframe{
 
  display: none;
  width: 100%;
  
}



.movie img {
  max-width: 100%;
}

.movie-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .3rem;
}

.movie-info h3 {
  margin: 0;
}

.movie-over {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(252, 251, 249, .9);
  transform: translateY(100%);
  color: black;
  padding: 1rem;
  font-size: .8rem;
  overflow: auto;
  max-height: 100%;
  transition: transform .5s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;


}
.trailer{
  padding: .5rem;
  border-radius: 5px;
  margin:1rem;
  cursor: pointer;
  font-size: 1.1rem;
  color: white;
  background-color: rgb(160, 4, 4);
  font-family: 'Mitr';
  font-weight: 600;
  
 
}
.trailer:hover{
  background-color: rgba(138, 4, 4, 0.808);
  transition: all .4s ease-in-out;
  border-radius: 10px;
   
   font-size: 1.2rem;
 
  
 
}

.movie:hover .movie-over {
  transform: translateY(0%);
}

@media (max-width:1025px){
  .movie {

   
    margin-left: 3.4rem;
    
  }
}
@media (max-width:800px){
  .movie {

   
    margin-left:1rem;
    width: 14rem;
    
  }
  .movie-over .trailer{
    font-size: 1rem;  
  }
 header .search{
   margin-left: 6rem;
 }
}
@media (max-width:500px){
  .movie {

   
    margin-left:1rem;
    width: 14rem;
    
  }
  .movie-over .trailer{
    font-size: 1rem;  
  }
 header{
   display: flex;
   flex-direction: column;
   align-items: center;
   padding: 0;

 }
 header h1{
   font-size: 2.2rem;
   margin-bottom: .2rem;
   
 }
 header form{
   width: 100%;
   align-items: center;
 }
 form .search {
   width: 80%;
   margin: 0 0  .9rem 2.5rem;
 }
 
 .movie {

 
   align-items: center;
  width: 75%;
  margin: 1rem 0 0 2rem;

 }
}